import MailerBoxDieline from './dieline/mailerBoxDieline'
import ShippingBoxDieline from './dieline/shippingBoxDieline'

export default function processOrderUps(order) {

  var box
  if (order.style === 'mailer box') {
    box = new MailerBoxDieline(order)
  } else if (order.style == 'shipping box') {
    box = new ShippingBoxDieline(order)
  } else {
    return {
      error: `${order.style} is not a supported style`
    }
  }

  const sheets = [
    {
      length: 48,
      width: 96,
    },
    // {
    //   length: 60,
    //   width: 100,
    // },
    // {
    //   length: 60,
    //   width: 120,
    // },
  ]

  order.ups = box.ups_for({sheets: sheets})

}
